.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: var(--header-height);
    gap: 0.2em;
    align-items: center;
    z-index: 5;

    background: var(--heading-color);
    color: var(--secondary-color);

    display: flex;
    align-items: center;
    justify-content: center;
}

.burger {
    display: none;
}


@media (max-width: 550px) {
    .header {
        height: var(--header-height);
    }

    .burger {
        display: block;
        position: absolute;
        top: 0.5em;
        right: 0.5em;
    }

}